const HeroSection = () => {
  return (
    <div className="relative mb-[60px] md:mb-[180px] mt-[40px] md:mt-[260px] heroSection transition-all">
      <div className="heroSection-a heroSection-elem">
        <img src="img/.webp" alt="hero" />
      </div>
      <div className="heroSection-b heroSection-elem">
        <img src="img/1.1.webp" alt="feature-1" />
      </div>
      <div className="heroSection-c heroSection-elem">
        <img src="img/2.2.webp" alt="feature-1" />
      </div>
      <div className="heroSection-d heroSection-elem">
        <img src="img/3.3.webp" alt="feature-1" />
      </div>
    </div>
  );
};

export default HeroSection;
